
















import Vue from 'vue'
import Component from 'vue-class-component'

@Component
export default class Pagination extends Vue {
  activePage = 1
  currentPages(n: number) {
    return (
      (this.activePage <= 2 && n <= 4) ||
      (this.activePage >= 8 && n >= 6) ||
      (this.activePage - n > -3 && this.activePage - n < 2)
    )
  }
}
